html {
  font-family: Cormorant, serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  -webkit-text-decoration: none;
  text-decoration: none;
  color: black;
}

ul {
  list-style: none;
}

main {
  padding: 0 3rem;
}

h1.header-logo {
  margin: 0;
  padding: 0;
  font-family: Cormorant, serif;
  font-size: 1rem;
  font-weight: 700;
}

h2 {
  color: #58697c;
  margin-bottom: 2rem;
}

p {
  color: #58697c;
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 1rem;
  line-height: 1.5rem;
}

.container {
  position: relative;
  max-width: 1920px;
  height: calc(100% - 1.6rem);
  margin: 0 auto;
}

.header {
  font-family: Cormorant, serif;
  font-weight: 400;
  margin-bottom: 4rem;
}
.header__inner {
  display: flex;
  gap: 2rem;
  padding: 1.6rem 0 0 1.6rem;
}

.header-menu {
  display: flex;
  gap: 2rem;
}

.header a {
  position: relative;
  white-space: nowrap;
}

.header a::before {
  background-color: black;
  position: absolute;
  content: "";
  width: 100%;
  height: 0.12rem;
  left: 0;
  bottom: -0.3rem;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform 0.3s;
}

.header a:hover::before {
  transform-origin: left;
  transform: scaleX(1);
  transition: transform 0.3s;
}

.social-media {
  position: relative;
  max-width: -moz-fit-content;
  max-width: fit-content;
  display: flex;
  align-items: center;
  gap: 1rem;
}
.social-media__icon {
  transition: all 0.3s;
}
.social-media:hover .social-media__icon {
  rotate: -15deg;
}
.social-media__link {
  font-weight: 700;
  color: #58697c;
  transition: color 0.2s ease-in-out;
}
.social-media__link:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.social-media__link:hover {
  color: black;
}

.footer__signature {
  padding: 1.6rem;
}

.projects,
.publications {
  display: grid;
  grid-template-columns: 1fr 4fr;
}
.projects__filter,
.publications__filter {
  min-width: 10rem;
  margin-bottom: 2rem;
}
.projects__catalog,
.publications__catalog {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 3rem;
  justify-content: space-evenly;
  min-height: -moz-fit-content;
  min-height: fit-content;
  padding-bottom: 5rem;
}
.projects__catalog-item,
.publications__catalog-item {
  position: relative;
  justify-self: center;
  align-self: start;
  max-width: 375px;
}
.projects__catalog-item-preview-img,
.publications__catalog-item-preview-img {
  width: 100%;
}
.projects__catalog-item-link::after,
.publications__catalog-item-link::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

@media (max-width: 1200px) {
  .projects__catalog,
  .publications__catalog {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 769px) {
  .projects__catalog,
  .publications__catalog {
    grid-template-columns: repeat(1, 1fr);
  }
}
@media (max-width: 610px) {
  .projects,
  .publications {
    grid-template-columns: 1fr;
    height: -moz-fit-content;
    height: fit-content;
  }
  .projects__catalog,
  .publications__catalog {
    grid-template-columns: repeat(1, 1fr);
  }
}
@media (max-width: 769px) {
  .header__inner {
    gap: 1.5rem;
  }
  .header-menu {
    gap: 1rem;
  }
}
@media (max-width: 481px) {
  main {
    padding: 0 1.5rem;
  }
}
nav {
  font-family: Cormorant, serif;
  font-size: 1rem;
  font-weight: 600;
}

h1.header-logo {
  font-weight: 700;
}

@media (max-width: 680px) {
  .header__inner,
  .header-menu {
    flex-wrap: wrap;
  }
}
.text--italic {
  font-style: italic;
}

.link--active::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0.12rem;
  left: 0;
  bottom: -0.3rem;
  transform-origin: right;
  transform: scaleX(1);
  background-color: #58697c;
